import React from 'react';

function Carousel() {

    return (
        <>
            <div id="carouselExampleIndicators" className="carousel slide" data-bs-ride="true">
                <div className="carousel-indicators">

                    <button type="button" data-bs-target="#carouselExampleIndicators" data-bs-slide-to="0" className="active" aria-current="true" aria-label="Slide 1">Network Protocol</button>
                    <button type="button" data-bs-target="#carouselExampleIndicators" data-bs-slide-to="1" aria-label="Slide 2">Gamestore</button>
                    <button type="button" data-bs-target="#carouselExampleIndicators" data-bs-slide-to="2" aria-label="Slide 3">NFT</button>
                </div>
                <div className="carousel-inner">
                    <div className="carousel-item active">
                        <img src={require("./images/networkprotocol.png")} className="d-block w-100" alt="..." />
                    </div>
                    <div className="carousel-item">
                        <img src={require("./images/3dmockup.png")} className="d-block w-100" alt="..." />
                    </div>
                    <div className="carousel-item">
                        <img src={require("./images/Artboard 12.png")} className="d-block w-100" alt="..." />
                        {/* <div className="nft-shadow"></div> */}
                    </div>
                </div>
                <button className="carousel-control-prev" type="button" data-bs-target="#carouselExampleIndicators" data-bs-slide="prev">
                    <span className="carousel-control-prev-icon" aria-hidden="true"></span>
                    <span className="visually-hidden">Previous</span>
                </button>
                <button className="carousel-control-next" type="button" data-bs-target="#carouselExampleIndicators" data-bs-slide="next">
                    <span className="carousel-control-next-icon" aria-hidden="true"></span>
                    <span className="visually-hidden">Next</span>
                </button>
            </div>
            {/* <Progress bgcolor={"#6a1b9a"} completed={completed} /> */}
        </>
    );
}


export default Carousel